import React, { useState } from "react";
import "./App.css";
import { Form } from "./Form";
import { Clock } from "./Clock";
import { currencies } from "./currencies";

function App() {
  const [result, setResult] = useState(); //Zadeklarowalimy sobie zmienn stanow result (bdzie to obiekt poniewa偶 musimy wypisa ile byo wczeniej ile jest teraz i jaka waluta zostaa przeliczona),

  const calculateResult = (currency, amount) => {
    // funkcja, kt贸ra szuka (metoda find) wr贸d tablicy walut (currencies) short taki jaki ma waluta i wycigamy z niej rate
    const rate = currencies.find(({ short }) => short === currency).rate;

    setResult({
      //nasz obiekt kt贸ry ma 3 waciwoci, kt贸re bd wypisane w wyniku
      sourceAmount: +amount, //Zmiana na liczb
      targetAmount: amount / rate, // Dziaanie 偶eby przeliczy targetAmount
      currency, //Waluta
    });
  };

  return (
    <div className="app">
      <Clock />
      <Form result={result} calculateResult={calculateResult} />
    </div>
  ); //W tej aplikacji jest Form do kt贸rego przekazujemy result i calculateResult
}

export default App;
